import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class BlogList extends Component {
    constructor(props) {
        super(props);

        this.state = {
            blogs: []
        }
    }

    async componentDidMount() {
        let response = await fetch("http://api.kylethomas.com.au:1337/blogs");
        if (!response.ok) {
            return
        }

        let blogs = await response.json()
        this.setState({blogs: blogs})
    }

    render() {
        return (
            <section className="project-list pt-4">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <h1>Welcome To My Blog</h1>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 font-light">
                            <p>My latest stories, tutorials and updates for developers.</p>
                        </div>
                    </div>
                    <div className="blogsList-container">
                        {this
                            .state
                            .blogs
                            .map((blog, index) => {
                                return (
                                    <div className="row mt-5" key={blog.id}>
                                        <div className="col-md-12">
                                            <h3>{blog.title}</h3>
                                            <div className="project-img">
                                            </div>
                                            <p className="font-light">{blog.tagLine}</p>
                                            <Link className="btn btn-blue-outline pl-4 pr-4" to="">Read More</Link>
                                        </div>
                                    </div>
                                );
                            })}
                    </div>
                </div>
            </section>
        );
    }
}

export default BlogList;